*{
  margin: 0px ;


}

body{
  padding: 0px   !important;
}


.localColor{
  background-color: #f9b524;
}
.colorText{
  color: #f9b524;
}

.cards-wrapper {
  display: flex;
  justify-content: center;
}
.card img {
  max-width: 100%;
  max-height: 100%;
}
.modalLocal{
 z-index: 9999;
  background-color:rgba(0, 0, 0, 0.6);
  position:  absolute;
  height: 100%;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  
}
.card {
  margin: 0 0.5em;
  box-shadow: 2px 6px 8px 0 rgba(22, 22, 26, 0.18);
  border: none;
  border-radius: 0;
}
.carousel-inner {
  padding: 1em;
}

.carousel-control-prev,
.carousel-control-next {
  background-color: #e1e1e1;
  width: 5vh;
  height: 5vh;
  border-radius: 50%;
  top: 50%;
  transform: translateY(-50%);
}


@media (min-width: 768px) {
  .card img {
    height: 11em;
  }
}
.pointer{
  cursor: pointer;
}


.alto{
  height: 450px !important;
  object-fit:cover;
}


.carousel-item .cards-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}
.circle-card {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  overflow: hidden;
}
.circle-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}